import React from "react";
import {
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  TableContainer,
  Paper,
} from "@mui/material";
import { Star, ArrowForwardIos, Person } from "@mui/icons-material";

// Dummy data
const rows = [
  { task: "Improve website copy", status: "Done", assignee: "", due: "02/10/2025" },
  { task: "Update help center & FAQ", status: "In progress", assignee: "", due: "02/12/2025" },
  { task: "Publish release notes", status: "Not started", assignee: "", due: "02/15/2025" },
];

// Status badge styled to mimic your screenshot
const StatusBadge = ({ status }) => {
  let bg = "";
  let text = "";
  switch (status) {
    case "Done":
      bg = "bg-green-100";
      text = "text-green-800";
      break;
    case "In progress":
      bg = "bg-blue-100";
      text = "text-blue-800";
      break;
    case "Not started":
      bg = "bg-gray-200";
      text = "text-gray-800";
      break;
    default:
      bg = "bg-gray-100";
      text = "text-gray-700";
  }
  return (
    <span className={`px-2 py-0.5 rounded-full text-xs font-medium ${bg} ${text}`}>
      {status}
    </span>
  );
};

export default function TasksTrackerUI() {
  return (
    <div className="p-6 max-w-4xl mx-auto font-sans">
      {/* Header */}
      <div className="flex items-center mb-4">
        <div className="text-green-600">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6"
            fill="currentColor"
            viewBox="0 0 24 24"
          >
            <path d="M9 16.2l-3.5-3.5 1.41-1.41L9 13.38l7.09-7.09L17.5 7l-8.5 8.5z" />
          </svg>
        </div>
        <h2 className="ml-2 text-xl font-semibold text-gray-800">Tasks Tracker</h2>
      </div>

      {/* Filter / Tabs bar */}
      <div className="flex items-center space-x-2 mb-6">
        <div className="flex items-center space-x-1 px-3 py-1.5 bg-gray-100 rounded-full text-sm font-medium text-gray-900">
          <Star fontSize="small" className="text-gray-600" />
          <span>All Tasks</span>
        </div>
        <div className="flex items-center space-x-1 px-3 py-1.5 rounded-full text-sm font-medium text-gray-600 hover:bg-gray-100 cursor-pointer">
          <ArrowForwardIos fontSize="inherit" style={{ fontSize: 14 }} />
          <span>By Status</span>
        </div>
        <div className="flex items-center space-x-1 px-3 py-1.5 rounded-full text-sm font-medium text-gray-600 hover:bg-gray-100 cursor-pointer">
          <Person fontSize="small" />
          <span>My Tasks</span>
        </div>
      </div>

      {/* Table */}
      <TableContainer component={Paper} className="rounded-md shadow-sm">
        <Table>
          <TableHead>
            <TableRow className="bg-gray-50">
              <TableCell className="text-xs font-medium text-gray-600">Task name</TableCell>
              <TableCell className="text-xs font-medium text-gray-600">Status</TableCell>
              <TableCell className="text-xs font-medium text-gray-600">Assignee</TableCell>
              <TableCell className="text-xs font-medium text-gray-600">Due Date</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, idx) => (
              <TableRow key={idx}>
                <TableCell className="text-sm text-gray-900">{row.task}</TableCell>
                <TableCell>
                  <StatusBadge status={row.status} />
                </TableCell>
                <TableCell className="text-sm text-gray-700">{row.assignee}</TableCell>
                <TableCell className="text-sm text-gray-700">{row.due}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}
